.loader {
    display:flex;
    justify-content:center;
    align-items:center;
    position: fixed;
    width: 100%;
    margin-top: 40vh;
}

.container {
    padding: 0;
    margin: 0;
    text-align: center;
}

.container h1 {
    font-size: 60px;
}

@media screen and (max-width:600px) {
    .container h1 {
        font-size: 40px;
    }
}